<template>
	<div class="bg">
		<el-card class="box-card">
			<div slot="header" class="clearfix">
				<span>登录</span>
			</div>
			<el-form :model="form" :rules="rules" ref="form" status-icon label-width="60px">
				<el-form-item label="账号" prop="username">
					<el-input type="text" v-model="form.username" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="密码" prop="password">
					<el-input type="password" v-model="form.password" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button :plain="true" @click="handleLogin" type="primary">登录</el-button>
				</el-form-item>
			</el-form>
			<div class="el-footer el-row--flex">
				<div>
					<el-link href="#/Register" type="info" :underline="false">注册账户</el-link>
				</div>
				<div>
					<el-link href="#/" type="info" :underline="false">忘记密码？</el-link>
				</div>
			</div>
		</el-card>
	</div>
</template>

<script>
// 按需导入 封装好的axios
import { Admin } from "@/api";
import global from '@/plugins/global' // 按需导入全局变量--控制动态路由请求  


export default {
	data() {
		return {
			form: {
				username: '',
				password: '',
			},
			rules: {
				username: [
					{ required: true, message: '请输入账户名称', trigger: 'blur' },
					{ min: 3, max: 30, message: '长度在 3 到 30 个字符', trigger: 'blur' }
				],
				password: [
					{ required: true, message: '请输入密码', trigger: 'blur' },
					{ pattern: /\d{3,}/, message: '密码至少3个数字!', trigger: 'blur' }
				],
			}
		}
	},
	created() {
	},
	methods: {
		handleLogin() {
			//表单验证
			this.$refs.form.validate(async (valid) => {
				if (valid) {
					// 发送Ajax
					// {...this.form} 浅拷贝 this.form
					// await 配合 async 使用，async放在await最近的函数上。
					let { status, msg, data } = await Admin.login({ ...this.form });

					if (status) {
						// 缓存数据
						sessionStorage.user_id = data.user_id;
						sessionStorage.role_id = data.role_id;
						sessionStorage.access_token = data.access_token;
						sessionStorage.refresh_token = data.refresh_token;
						// 动态路由重新加载
						global.is_route_unload = true;
						// 信息提示
						this.$message.success(msg);
						// 获取地址栏参数 -- 根据重定向判断跳转页面
						let { redirect } = this.$route.query;
						if (redirect) {
							// 重定向至目标地址
							this.$router.push(redirect);
						} else {
							// 默认跳转页面 -- 组件中使用 -- 路由跳转
							this.$router.push('/article/list');
						}
					} else {
						// 使用后台返回的信息 msg -- 信息可能不同
						this.$message.error(msg);
					}
				} else {

				}
			})
		},
	}
}
</script>

<style lang="less" scoped>
.bg {
	height: 100vh;
	background: url('./img/login-bg.jpg') center no-repeat;
	background-size: cover;

	.box-card {
		position: fixed;
		right: 50px;
		top: 50%;
		transform: translateY(-50%);
		width: 450px;

		.el-footer {
			justify-content: space-between;
		}
	}
}
</style>
